import React from 'react';
import AdminService from "../service/AdminService";
import LoginNavBar from "../LoginNavbar";
import Grid from '@material-ui/core/Grid';
import InputLabel from '@material-ui/core/InputLabel';
import LoginNewUIShoonya from './LoginNewUIShoonya';
import { resolveResponse } from "../../utils/ResponseHandler";
import Notify from "../../utils/Notify";
import { Button } from '@material-ui/core';

class LoginComponent extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            userName: "FA35674",
            password: "Vivan@2023#",
            isDasable: false,
            isError: false,
            totp: "",
            api_secret: "ca7d0433f62e27a4bea6853296394528", 
            selectedAc: "Vijay"
        };
        this.login = this.login.bind(this);

    }


    render() {

        return (
            <React.Fragment>
                <LoginNavBar />

                {/* <Container maxWidth="sm">
                     <br/><br/><br/> 
                    <Typography variant="h4" style={styles.label}>Login</Typography>
                    <form style={styles.formStyle}>
                        <TextField type="text"  required={true} label="Olms Id" fullWidth margin="normal" name="userName" value={this.state.userName}  onChange={this.onChange}/>

                        <TextField type="password"  required={true} label="Password" fullWidth margin="normal" name="password" value={this.state.password} onChange={this.onChange}/>
                        <Grid item  xs={12} sm={8}>
                            <Button disabled={this.state.isDasable}  variant="contained" color="primary" onClick={this.login}>Login</Button>
                        </Grid>
                        <Grid item  xs={12} sm={10}>
                             {this.state.isDasable ? <InputLabel variant="subtitle1" style={styles.waitMessage}> Please wait...</InputLabel> :""} 
                             {this.state.isError ? <InputLabel variant="subtitle1" style={styles.errorMessage}> {this.state.isError} </InputLabel>: ""}  
                        </Grid>
                    </form>
                </Container> */}

                {/* New Login UI */}
                
                <LoginNewUIShoonya loginProps={{ onChange: this.onChange, login: this.login, userName: this.state.userName, password: this.state.password, totp: this.state.totp, api_secret: this.state.api_secret,  updateMinuData: this.updateMinuData,  selectedAc: this.state.selectedAc }} />
                
                <Grid container justify="space-around">
                    <Grid justify={"center"} container xs={12} sm={10}>
                        {this.state.isDasable ? <InputLabel variant="subtitle1" style={styles.waitMessage}> Please wait...</InputLabel> : ""}
                        {this.state.isError ? <InputLabel variant="subtitle1" style={styles.errorMessage}> {this.state.isError} </InputLabel> : ""}
                    </Grid>
                </Grid>
            </React.Fragment>


        )

    }

    componentDidMount() {
        const shoonyaLoggedIn = window.localStorage.getItem("shoonyaLoggedIn");
        if (shoonyaLoggedIn === 'yes') {
            //  const lastUrl = localStorage.getItem("lastUrl"); 
            this.props.history.push('position-shoonya');
        }

        if (document.location.href.includes('logout')) {
            document.location.href = '#/login-shoonya';
            window.location.reload(true);
        } else {
        }

    }

    updateMinuData = () => {
       
        if(this.state.selectedAc == "Vijay"){
            this.setState({ 
                userName: "FA74484",
                password: "Mkv@2022",
                api_secret: "a5177adf7148eb2db5c9a20789d5a331",
                selectedAc: "Minu"  
            })
        }else if(this.state.selectedAc == "Minu") { 
            this.setState({ 
                userName: "FA35674",
                password: "Vivan@2023#",
                api_secret: "ca7d0433f62e27a4bea6853296394528", 
                selectedAc: "Vijay"
            })
        }
       
    }

    onChange = (e) => {
        this.setState({ [e.target.name]: e.target.value.trim() });
    }

    login = (e) => {

        this.setState({ isError: "" });


        e.preventDefault();

        if (!this.state.userName && !this.state.password) {
            this.setState({ isError: "User Id and Password are required." });
            return;
        }

        if (!this.state.userName) {
            this.setState({ isError: "User Id is required." });
            return;
        }
        if (!this.state.password) {
            this.setState({ isError: "Password is required." });
            return;
        }

        this.setState({ isDasable: true });


        const loginPayload = {
            userid: this.state.userName,
            password: this.state.password,
            twoFA: this.state.totp, 
            api_secret: this.state.api_secret, 
            
        };
        AdminService.shoonyaLogin(loginPayload)
            .then(loginRes => {
                //  Notify.showError("Olms Id and password is required.");
                //  alert(JSON.stringify(res));
                //   console.log("res",loginRes); 

                loginRes = loginRes && loginRes.data;
                //  console.log("resdata",loginRes); 
                if (loginRes.stat === "Not_Ok") {
                    this.setState({ isError: loginRes.emsg });
                    Notify.showError(loginRes.emsg);
                } else {
                    // data = res.data; 
                    this.setState({ isDasable: false });
                    window.localStorage.setItem("shoonyaLoggedIn", 'yes');
                    localStorage.setItem('uname', loginRes.uname);
                    //    this.props.history.push('/position');

                    AdminService.getStaticData().then(res => {
                        console.log(res)
                        var data = res.data;
                        //data = JSON.parse(data);   
                        localStorage.setItem('staticData', JSON.stringify(data));

                        var totalWatchlist = Object.keys(data);
                        this.setState({ totalWatchlist: totalWatchlist });
                        localStorage.setItem('totalWatchlist', JSON.stringify(totalWatchlist));

                        this.setState({ staticData: data });

                        var watchlist = [];
                        totalWatchlist.forEach(element => {

                            var mylist = data[element];
                            mylist.forEach(element2 => {
                                var foundInWatchlist = watchlist.filter(row => row.token === element2.token);
                                if (!foundInWatchlist.length) {
                                    watchlist.push(element2);
                                }
                            });
                        });

                        localStorage.setItem('watchList', JSON.stringify(watchlist));

                     //   window.location.replace('#/position-shoonya')

                       window.location.replace('#/sector-heat-map2')

                     

                    });
                
                }


            });

        // setTimeout(() => {
        //     this.setState({ isError: "" });
        // }, 3000);



    }

}

const styles = {
    formStyle: {
        display: 'flex',
        flexFlow: 'row wrap'
    },
    label: {
        display: 'flex',
        justifyContent: 'center'
    },
    errorMessage: {
        color: "red",
        marginTop: '11px'
    },
    waitMessage: {
        color: "gray",
        marginTop: '11px'

    }

}

export default LoginComponent;