    getDayDefaultProfitPer() {
        let today = moment().isoWeekday();
        let optionProfit = 2;
        if (today === 5) {
            optionProfit = 2;
        }
        else if (today === 1) {
            optionProfit = 4;
        }
        else if (today === 2) {
            optionProfit = 6;
        }if (today === 3) {
            optionProfit = 8;
        }  if (today === 4) {
            optionProfit = 10;
        } 
        return optionProfit/2;
      }

      getDayDefaultLossPer() {
        let today = moment().isoWeekday();
        let optionProfit = -4;
        if (today === 5) {
            optionProfit = -5;
        }
        else if (today === 1) {
            optionProfit = -6;
        }
        else if (today === 2) {
            optionProfit = -8;
        }if (today === 3) {
            optionProfit = -10;
        }  if (today === 4) {
            optionProfit = -12;
        } 
        return optionProfit;
      }

    deleteActiveOptionStock = (row) => {
        var isDeleted = false;
        for (let index = 0; index < this.state.activeStockOptions.length; index++) {
            const element = this.state.activeStockOptions[index];
            if (row.name == element.name) {
                var delitem = this.state.activeStockOptions.splice(index, 1);
                if (delitem[0].name == row.name) {
                    isDeleted = true;
                }
                this.setState({ activeStockOptions: this.state.activeStockOptions });
                break;
            }
        }
        return isDeleted;
    }


