    calculateTradeExpence = (element) => {

        var totalbuyvalue = parseFloat(element.totalbuyavgprice) * parseInt(element.buyqty);
        var buyCharge = parseFloat(totalbuyvalue) * 0 / 100;
        buyCharge = 0;

        var totalsellvalue = parseFloat(element.totalsellavgprice) === 0 ? parseFloat(element.totalbuyavgprice) : parseFloat(element.totalsellavgprice);
        totalsellvalue = totalsellvalue * element.sellqty;
        var sellCharge = parseFloat(totalsellvalue) * 0 / 100;
        sellCharge = 0;

        let turnOver = totalbuyvalue + totalsellvalue;
        let totalBroker = buyCharge + sellCharge;

        let sst = 0.025; //stock intraday
        sst = 0.05; //option 

        let sstCharge = totalsellvalue * sst / 100;
        let transCharge = turnOver * 0.053 / 100;
        let stampDuty = totalbuyvalue * 0.003 / 100;
        let sebiCharge = turnOver * 10 / 10000000;
        let gstCharge = (totalBroker + transCharge + sebiCharge) * 18 / 100;

        let total = totalBroker + sstCharge + transCharge + sebiCharge + gstCharge + stampDuty;

        var chargeInfo = {
            tradeExpence: total,
            totalbuyvalue: totalbuyvalue,
            totalsellvalue: totalsellvalue,
            turnOver: turnOver,
            expenceInfo: "Brokerage: " + totalBroker.toFixed(2) + " \nSTT: " + sstCharge.toFixed(2) + " \nTransaction: " + transCharge.toFixed(2) + " \nSebi: " + sebiCharge.toFixed(2) + '\nStamp Duty:' + stampDuty + " \nGST: " + gstCharge.toFixed(2) + " \n\nTotal: " + total.toFixed(2)
        }
        return chargeInfo;
    }

