    bankniftyTrackOrder = () => {
        const activeStockOptions = localStorage.getItem('activeStockOptions') && JSON.parse(localStorage.getItem('activeStockOptions')) || [];

        if (activeStockOptions.length > 0) {
            for (let index = 0; index < activeStockOptions.length; index++) {
                const element = activeStockOptions[index];
                if (element.optiontype == 'CE' && element.optionStockStoploss && this.state.liveBankniftyLtdData.iv < element.optionStockStoploss) {
                    this.deleteIndexOption(element, index);
                } else if (element.optiontype == 'CE' && element.optionStockTarget && this.state.liveBankniftyLtdData.iv >= element.optionStockTarget) {
                    this.deleteIndexOption(element, index);
                } else if (element.optiontype == 'PE' && element.optionStockStoploss && this.state.liveBankniftyLtdData.iv > element.optionStockStoploss) {
                    this.deleteIndexOption(element, index);
                } else if (element.optiontype == 'PE' && element.optionStockStoploss && this.state.liveBankniftyLtdData.iv <= element.optionStockTarget) {
                    this.deleteIndexOption(element, index);
                }
            }
        }
    }


