import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Switch from '@material-ui/core/Switch';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import FormGroup from '@material-ui/core/FormGroup';
import Typography from "@material-ui/core/Typography";
import AdminService from "../components/service/AdminService";
import buyOrderAddedTone from "./buy-order-added.ogg";
import { Button } from '@material-ui/core';

const {EMA, SMA} = require('technicalindicators'); 

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  menuButton: {
    marginRight: theme.spacing(2),
  },
  title: {
    flexGrow: 1,
  },
}));

export default function MenuAppBar({updateTheOrderList}) {
  const classes = useStyles();
  const [emaSellSwitch, setemaSellSwitch] = React.useState(localStorage.getItem('emaSellSwitch') == 'true' ? true : false);
  const [timeInterval, setTimeInterval] = React.useState(5)
  const [candleCount, setCandleCount] = React.useState(5)
  const [interval, setinterval] = React.useState('')
  const [smaValue, setsmaValue] = React.useState('')
  const [time, setTime] = React.useState('')

  const handleChange = (event) => {
    setemaSellSwitch(event.target.checked);
    localStorage.setItem('emaSellSwitch', event.target.checked);
  };
  const calculateEMA = (action) => {
    let starttime = (candleCount * timeInterval * 60000) + (72 * 60 * 60000);

    let data = {
          "exchange": "NSE",
          "token": '26009',
          "interval": timeInterval , 
          "starttime": Math.floor((new Date().getTime() - starttime) / 1000).toString(),
          "endtime":  new Date(new Date()).getTime()/1000  
      }

      AdminService.getHistoryShoonya(data).then(res => {
          var candleData = res.data;
          candleData && candleData.reverse();

          if (candleData && candleData.length > 0 ) {

              var closeData = [],  smadata = []; 
              candleData.forEach(element => {
                  closeData.push(parseFloat(element.intc));
                  smadata.push({close : parseFloat(element.intc), time: element.time});
              });
              let smaResult = EMA.calculate({ period: candleCount, values: closeData });
           //   let smaResult  = SMA.calculate({ period: 5, values: closeData }); 
              console.log('closeData', closeData, 'smaResult', smaResult); 
              let lastCandle = candleData[candleData.length-1]; 
              setsmaValue((smaResult[smaResult.length-1]).toFixed(2));
              if(action != 'justCalculate'){
                if(parseFloat(lastCandle.intc) < parseFloat(smaResult[smaResult.length-1]) && parseFloat(lastCandle.intc) < parseFloat(lastCandle.into)){
                  const orderInput = {
                    createdAt: new Date().toLocaleTimeString(),
                    sellAt: parseFloat(lastCandle.intl)-5,
                    sellAtStoploss: parseFloat(lastCandle.inth)+5,
                    //sellAtTarget: parseFloat(lastCandle.intl) - 55,
                    id: new Date().getTime(), 
                    origin: "EmaSell"
                  }
                  let orderOptionList =  localStorage.getItem('orderOptionList') && JSON.parse(localStorage.getItem('orderOptionList')) || [];
                  
                  let foundIndex = orderOptionList.findIndex(x => x.origin === 'EmaSell');
                  orderOptionList.splice(foundIndex, 1);

                  orderOptionList.push(orderInput); 
                  localStorage.setItem('orderOptionList', JSON.stringify(orderOptionList));
                  new Audio(buyOrderAddedTone).play();
                  updateTheOrderList();
                }
              }
    
          } else {
              console.log("bn candle data emply");
          }
      })    
  };
  

  React.useEffect(() => {
    if(emaSellSwitch){
      setinterval(setInterval(() => {
        setTime(new Date().toLocaleTimeString());
      //  console.log("ema interval running", new Date().toLocaleTimeString())
        if (new Date().getMinutes() % timeInterval == 0 && new Date().getSeconds() == 0) {
          calculateEMA();
      }
      }, 1000)); 
    }else{
      console.log('emaSellSwitch', emaSellSwitch)

      clearInterval(interval);
    }
  }, [emaSellSwitch])


  return (
    <div className={classes.root}>
      
      <FormGroup>
      <Typography color="primary" gutterBottom>
            EMA Sell | <Button size='small' onClick={() => calculateEMA('justCalculate')}> Value: {smaValue} </Button>
        </Typography>

        <FormControlLabel
        style={{marginTop: "-15px"}}
          control={<Switch checked={emaSellSwitch} onChange={handleChange} aria-label="EMA BUY Auto" />}
          label={localStorage.getItem('emaSellSwitch') == 'true' ? 'EMA BUY Auto ON ' + time : ' EMA BUY Auto OFF '+ localStorage.getItem('emaSellSwitch')} 
        /> 
         <Typography variant={'string'} gutterBottom>
          Settings  &nbsp; Time Interval &nbsp;
          <input
            type="number"
            style={{ width: '30px', textAlign: "center" }}
            value={timeInterval}
            onChange={(event) => setTimeInterval(event.target.value)}
          />
          &nbsp; Candle Count &nbsp;
          <input
            type="number"
            style={{ width: '30px', textAlign: "center" }}
            value={candleCount}
            onChange={(event) => setCandleCount(event.target.value)}
          />

         </Typography>
      </FormGroup>
   
    </div>
  );
}
