import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Switch from '@material-ui/core/Switch';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import FormGroup from '@material-ui/core/FormGroup';
import AdminService from "../components/service/AdminService";
import Typography from "@material-ui/core/Typography";
import { Button } from '@material-ui/core';

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  menuButton: {
    marginRight: theme.spacing(2),
  },
  title: {
    flexGrow: 1,
  },
}));

export default function MenuAppBar() {
  const classes = useStyles();
  const [collectSmallFlag, setCollectSmallFlag] = React.useState('')
  const [timeInterval, setTimeInterval] = React.useState(5)
  const [candleCount, setCandleCount] = React.useState(5)
  const [emaLastValue, setEmaLastValue] = React.useState('')

  React.useEffect(() => {
    AdminService.getCollectSmallFlag().then(res => {
      console.log('getCollectSmallFlag res', res.data);
      if(res.data){
        setCollectSmallFlag(res.data.collectSmallFlag)
      } 
    })
  }, []);

  const handleChange = (event) => {
    setCollectSmallFlag(event.target.checked);       
    let param = { 
      collectSmallFlag : event.target.checked, 
      timeInterval : timeInterval, 
      candleCount : candleCount, 
    }; 
    AdminService.updateCollectSmallFlag(param).then(res => {
      if(res.data){
        console.log('collectSmallFlag res', res.data.collectSmallFlag);
        setCollectSmallFlag(res.data.collectSmallFlag)
        setEmaLastValue(res.data.emaLastValue.toFixed(2))
      } 
    }).catch(error => alert(" Switch fail to update"+ error))  
  };

  const checkEMAValue = () => {
    let param = { 
      timeInterval : timeInterval, 
      candleCount : candleCount, 
    }; 
    AdminService.checkEMAValue(param).then(res => {
      if(res.data){
        setEmaLastValue(res.data.emaLastValue.toFixed(2))
      } 
    }).catch(error => alert(" Switch fail to update"+ error))  
  };

  return (
    <div className={classes.root}>
      <FormGroup>
      <Typography color="primary" gutterBottom>
                    Collect 100+ ( profit) 
                    
                    <Button onClick={() => checkEMAValue()}> EMA </Button>
                    
                    {emaLastValue ? emaLastValue : ''}
                  
              </Typography>
        <FormControlLabel
          control={<Switch checked={collectSmallFlag} onChange={handleChange} aria-label=" profit Auto Trading switch" />}
          label={collectSmallFlag ? " Switch ON":  " Switch: OFF"} 
        /> 
         <Typography variant={'subtitle2'} gutterBottom>
          Settings  &nbsp; Time Interval &nbsp;
          <input
            type="number"
            style={{ width: '30px', textAlign: "center" }}
            value={timeInterval}
            onChange={(event) => setTimeInterval(event.target.value)}
          />
          &nbsp; Candle Count &nbsp;
          <input
            type="number"
            style={{ width: '30px', textAlign: "center" }}
            value={candleCount}
            onChange={(event) => setCandleCount(event.target.value)}
          />

         </Typography>
      
      </FormGroup>
    </div>
  );
}
