import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Switch from '@material-ui/core/Switch';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import FormGroup from '@material-ui/core/FormGroup';

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  menuButton: {
    marginRight: theme.spacing(2),
  },
  title: {
    flexGrow: 1,
  },
}));

export default function MenuAppBar() {
  const classes = useStyles();
  const [isAutoTrading, setIsAutoTrading] = React.useState(localStorage.getItem('isAutoTrading') == 'true' ? true : false);

  const handleChange = (event) => {
    setIsAutoTrading(event.target.checked);
    localStorage.setItem('isAutoTrading', event.target.checked);
  };


  return (
    <div className={classes.root}>
      <FormGroup>
        <FormControlLabel
          style={{marginTop: "-15px"}}
          control={<Switch checked={isAutoTrading} onChange={handleChange} aria-label="Auto Trading switch" />}
          label={localStorage.getItem('isAutoTrading') == 'true' ? 'Auto Trading ON ' + localStorage.getItem('isAutoTrading')  : 'Auto Trading OFF '+ localStorage.getItem('isAutoTrading')} 
        /> 
      </FormGroup>
   
    </div>
  );
}
