import React, { useContext, useEffect, useState } from 'react';
import PropTypes from 'prop-types';
import AdminService from "../service/AdminService";
import Typography from "@material-ui/core/Typography";
import Button from "@material-ui/core/Button";

import ButtonGroup from "@material-ui/core/ButtonGroup";

import Table from "@material-ui/core/Table";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import TableCell from "@material-ui/core/TableCell";
import TableBody from "@material-ui/core/TableBody";
import Grid from "@material-ui/core/Grid";
import Paper from "@material-ui/core/Paper";
import PostLoginNavBar from "../PostLoginNavbar";
import { resolveResponse } from "../../utils/ResponseHandler";
import Spinner from "react-spinner-material";
import TextField from "@material-ui/core/TextField";
import Autocomplete from '@material-ui/lab/Autocomplete';
import DeleteIcon from '@material-ui/icons/Delete';
import OpenInNewIcon from '@material-ui/icons/OpenInNew';
import CommonMethods from '../../utils/CommonMethods';
import * as moment from 'moment';
import Notify from "../../utils/Notify";
import ShowChartIcon from '@material-ui/icons/ShowChart';
import ClearIcon from '@material-ui/icons/Clear';
import AutoTradingSwitch from "../../utils/AutoTradingSwitch";
import EmaBuySwitch from "../../utils/EmaBuySwitch";
import EmaSellSwitch from "../../utils/EmaSellSwitch";
import CrossMABuySellSwitch from "../../utils/CrossMABuySellSwitch";

import CollectSmallProfitSwitch from "../../utils/CollectSmallProfitSwitch";
import buyOrderAddedTone from "../../utils/buy-order-added.ogg";
import orderPlacedTone from "../../utils/order-placed.mp3";

const BNOptionBuyAtLevel = React.memo(({
  LiveLtp, refreshOrderbookFlagOn
}) => {

  const [buyAt, setBuyAt] = useState('');
  const [buyAtStoploss, setBuyAtStoploss] = useState('');
  const [buyAtTarget, setBuyAtTarget] = useState('');

  const [buyAtBelow, setBuyAtBelow] = useState('');
  const [buyAtBelowStoploss, setBuyAtBelowStoploss] = useState('');
  const [buyAtBelowTarget, setBuyAtBelowTarget] = useState('');

  const [sellAt, setSellAt] = useState('');
  const [sellAtStoploss, setSellAtStoploss] = useState('');
  const [sellAtTarget, setSellAtTarget] = useState('');

  const [sellAtAbove, setSellAtAbove] = useState('');
  const [sellAtAboveStoploss, setSellAtAboveStoploss] = useState('');
  const [sellAtAboveTarget, setSellAtAboveTarget] = useState('');

  const [orderOptionList, setOrderOptionList] = useState(localStorage.getItem('orderOptionList') && JSON.parse(localStorage.getItem('orderOptionList')) || []);
  const [deleteId, setDeleteId] = useState('');
  const [edited, setEdited] = useState(false);
  const [strikeLegCE, setstrikeLegCE] = useState(getdefaultstrikeLeg());
  const [strikeLegPE, setstrikeLegPE] = useState(getdefaultstrikeLeg());

  const [noOfLotCE, setnoOfLotCE] = useState(1);
  const [noOfLotCESuggested, setnoOfLotCESuggested] = useState(1);
  const [noOfLotPESuggested, setnoOfLotPESuggested] = useState(1);

  const [noOfLotPE, setnoOfLotPE] = useState(1);

  const [ceLtp, setCeLtp] = useState(0);
  const [peLtp, setPeLtp] = useState(0);
  const [alertPrice, setAlertPrice ] = useState('');
  const [alertPriceDown, setalertPriceDown ] = useState('');

  

  const placeOptionSPLevelOver = (spotPrice, optionType, spottype, elementInfo) => {
    let strikePrice = getStrikePrice(spotPrice, optionType);
    let nextExpiryOption = getNextExpiryOption(strikePrice, optionType);
    console.log(strikePrice, nextExpiryOption);
    let expiry = moment(nextExpiryOption.expiry).format('DDMMMYY');


    let buyOptionType = optionType === "CE" ? 'C' : "P";
    let lots = optionType === "CE" ? noOfLotCE : noOfLotPE;

    let tradingsymbol = `BANKNIFTY${expiry.toUpperCase()}${buyOptionType}${strikePrice}`;  //BANKNIFTY15SEP22C40400 "BANKNIFTY15SEP22C41300"
    if (nextExpiryOption) {
      let optionInput = {
        'buy_or_sell': 'B',
        'product_type': 'M',
        'exchange': 'NFO',
        'tradingsymbol': tradingsymbol,
        'quantity': lots * 25,
        'discloseqty': 0,
        'price_type': 'MKT',
        'price': 0
      }
      console.log('optionInput', optionInput);
     

      let spotDetails = {};
      spotDetails.tradingsymbol = tradingsymbol;
      spotDetails.optiontype = optionType;

      if (spottype == 'buyAt') {
        spotDetails.optionStockStoploss = elementInfo.buyAtStoploss;
        spotDetails.optionStockTarget = elementInfo.buyAtTarget;
      }

      if (spottype == 'buyAtBelow') {
        spotDetails.optionStockStoploss = elementInfo.buyAtBelowStoploss;
        spotDetails.optionStockTarget = elementInfo.buyAtBelowTarget;
      }

      if (spottype == 'sellAt') {
        spotDetails.optionStockStoploss = elementInfo.sellAtStoploss;
        spotDetails.optionStockTarget = elementInfo.sellAtTarget;
      }
      if (spottype == 'sellAtAbove') {
        spotDetails.optionStockStoploss = elementInfo.sellAtAboveStoploss;
        spotDetails.optionStockTarget = elementInfo.sellAtAboveTarget;
      }

      if (spottype == 'market' && optionType == 'CE') {
        spotDetails.optionStockStoploss = spotPrice - 100;
        spotDetails.optionStockTarget = spotPrice + 100;
      }

      if (spottype == 'market' && optionType == 'PE') {
        spotDetails.optionStockStoploss = spotPrice + 100;
        spotDetails.optionStockTarget = spotPrice - 100;
      }

      const activeStockOptions = localStorage.getItem('activeStockOptions') && JSON.parse(localStorage.getItem('activeStockOptions')) || [];
      // delete if any existing sl 
      for (let i = activeStockOptions.length - 1; i >= 0; i--) {
        if (activeStockOptions[i].tradingsymbol === optionInput.tradingsymbol) {
          activeStockOptions.splice(i, 1);
        }
      }
      activeStockOptions.push(spotDetails);
      localStorage.setItem("activeStockOptions", JSON.stringify(activeStockOptions));


      if (elementInfo && elementInfo.id) {
        setDeleteId(elementInfo.id);
      }
     
      new Audio(orderPlacedTone).play();
      localStorage.setItem(tradingsymbol, '');

      AdminService.shoonyaPlaceOrder(optionInput).then(res => {
        
       // refreshOrderbookFlagOn();
        setTimeout(() => {
          AdminService.getOrderListShoonya()
          .then((res) => {
            var orderlist = res.data;
          //  console.log('orderlist 0', orderlist[0]); 
            for (let index = 0; index < orderlist.length; index++) {
              const element = orderlist[index];
              if(element.tsym == tradingsymbol && element.trantype == "B" && element.status == "COMPLETE") {
                localStorage.setItem(tradingsymbol, element.avgprc);
                break; 
              }
            }
            
            console.log('orderlist updated by level', orderlist); 
            localStorage.setItem('oderbookData', JSON.stringify(orderlist));
          });
        }, 1000);
      })

    } else {
      Notify.showError("Option token not found for " + strikePrice + " update latest tokens")
    }

  }

  const speckIt = (text) => {
    var msg = new SpeechSynthesisUtterance();
    msg.text = text.toString();
    window.speechSynthesis.speak(msg);
  }
  const getNextExpiryOption = (strikePrice, optionType) => {
    let optionList = localStorage.getItem('staticData') && JSON.parse(localStorage.getItem('staticData')).NIFTYBANK_LATEST_OPTIONS || [];
    let filteredOptions = optionList.filter(item => item.strike / 100 === strikePrice && item.symbol.endsWith(optionType));
    let nextDate = moment(new Date()).add(8, 'days');
    let found = filteredOptions.filter(element => moment(element.expiry) <= nextDate);
    return found && found[0];
  }

  function getdefaultstrikeLeg() {
    let today = moment().isoWeekday();
    let strikeLeg = 0;
    if (today === 5 || today === 1) {
      strikeLeg = -4;
    }
    else if (today === 2) {
      strikeLeg = -3;
    }
    else if (today === 3) {
      strikeLeg = -1;
    } else {
      strikeLeg = 0
    }
    return 0;
  }
  const getStrikePrice = (spotPrice, optionType) => {
    let today = moment().isoWeekday();
    let strikePrice = 0;
    if (optionType === 'CE') {
      strikePrice = (Math.round(spotPrice) - Math.round(spotPrice) % 100) - (100 * strikeLegCE);
    } else if (optionType === 'PE') {
      strikePrice = (Math.round(spotPrice) - Math.round(spotPrice) % 100) + (100 * strikeLegPE);
    }

  //   if (optionType === 'CE') {
  //     if(Math.round(spotPrice) % 100 > 50){
  //       strikePrice = (Math.round(spotPrice) + 100 - Math.round(spotPrice) % 100) - (100 * strikeLegCE);
  //     }else{
  //       strikePrice = (Math.round(spotPrice) - Math.round(spotPrice) % 100) - (100 * strikeLegCE);
  //     }
  //   } else if (optionType === 'PE') {
  //     if(Math.round(spotPrice) % 100 > 50){
  //       strikePrice = (Math.round(spotPrice) + 100 - Math.round(spotPrice) % 100) + (100 * strikeLegCE);
  //     }else{
  //       strikePrice = (Math.round(spotPrice) - Math.round(spotPrice) % 100) + (100 * strikeLegCE);
  //     }
  //  //   strikePrice = (Math.round(spotPrice) - Math.round(spotPrice) % 100) + (100 * strikeLegPE);
  //   }
    return strikePrice;
  }

  useEffect(() => {
    if (deleteId) {
      let foundIndex = orderOptionList.findIndex(x => x.id === deleteId);
      orderOptionList.splice(foundIndex, 1);
      setDeleteId('');
      setOrderOptionList(orderOptionList)
      localStorage.setItem('orderOptionList', JSON.stringify(orderOptionList));
    }

  }, [deleteId, orderOptionList, setDeleteId]);

  useEffect(() => {
    if (orderOptionList || edited) {
      setOrderOptionList(orderOptionList)
      localStorage.setItem('orderOptionList', JSON.stringify(orderOptionList));
      setEdited(false);
    }
  }, [orderOptionList, edited]);

  useEffect(() => {
    if (LiveLtp && LiveLtp.iv) {
      orderOptionList.forEach(element => {
        if (element.buyAt && LiveLtp.iv >= parseFloat(element.buyAt)) {
          placeOptionSPLevelOver(LiveLtp.iv, 'CE', 'buyAt', element);
        } else if (element.buyAtBelow && LiveLtp.iv <= parseFloat(element.buyAtBelow)) {
          placeOptionSPLevelOver(LiveLtp.iv, 'CE', 'buyAtBelow', element);
        } else if (element.sellAt && LiveLtp.iv <= parseFloat(element.sellAt)) {
          placeOptionSPLevelOver(LiveLtp.iv, 'PE', 'sellAt', element);
        } else if (element.sellAtAbove && LiveLtp.iv >= parseFloat(element.sellAtAbove)) {
          placeOptionSPLevelOver(LiveLtp.iv, 'PE', 'sellAtAbove', element);
        }
      });

      if(alertPrice && LiveLtp.iv >= alertPrice){
        speckIt('Bank nifty above ' + parseInt(LiveLtp.iv))
        setAlertPrice('');
      }
      if(alertPriceDown && LiveLtp.iv <= alertPriceDown){
        speckIt('Bank nifty below' + parseInt(LiveLtp.iv))
        setalertPriceDown('');
      }
      
      higherHighLowerLowAutoTrade(); 
      collectSmallProfitAutoTrade();
    }

  }, [LiveLtp, orderOptionList, placeOptionSPLevelOver])

  useEffect(() => {
    getStrikePremium('CE');
  }, [strikeLegCE])

  useEffect(() => {
    getStrikePremium('PE');
  }, [strikeLegPE])

  const higherHighLowerLowAutoTrade = () => {

    if(localStorage.getItem('isAutoTrading') == 'true'){
      if(LiveLtp.takeHigherHighTrade){
        placeOptionSPLevelOver(LiveLtp.iv, 'CE', 'market')
        speckIt('buy order placed')
      }

      if(LiveLtp.takeLowerLowTrade){
        placeOptionSPLevelOver(LiveLtp.iv, 'PE', 'market')
        speckIt('sell order placed')
      }
    }

  }

  const collectSmallProfitAutoTrade = () => {

    if(localStorage.getItem('isAutoTradingSmallProfit') == 'true'){
    
      AdminService.get_time_price_series(26009).then(res => {
        console.log('order res', res); 
      })

      if(LiveLtp.takeHigherHighTrade){
        placeOptionSPLevelOver(LiveLtp.iv, 'CE', 'market')
        speckIt('khudra buy order placed')
      }

      if(LiveLtp.takeLowerLowTrade){
        placeOptionSPLevelOver(LiveLtp.iv, 'PE', 'market')
        speckIt('khudra sell order placed')
      }
    }

  }

  const addInOrderPenidngList = () => {
    if (buyAt || buyAtBelow || sellAt || sellAtAbove) {
      const orderInput = {
        createdAt: new Date().toLocaleTimeString(),
        buyAt: buyAt,
        buyAtStoploss: buyAtStoploss,
        buyAtTarget: buyAtTarget,

        buyAtBelow: buyAtBelow,
        buyAtBelowStoploss: buyAtBelowStoploss,
        buyAtBelowTarget: buyAtBelowTarget,

        sellAt: sellAt,
        sellAtStoploss: sellAtStoploss,
        sellAtTarget: sellAtTarget,

        sellAtAbove: sellAtAbove,
        sellAtAboveStoploss: sellAtAboveStoploss,
        sellAtAboveTarget: sellAtAboveTarget,

        id: new Date().getTime()
      }
      setOrderOptionList([...orderOptionList, orderInput]);
      new Audio(buyOrderAddedTone).play();
      resetInput();
    }
  }

  const orderValueChange = (e) => {
    const nameId = e.target.name.split('-');
    var foundIndex = orderOptionList.findIndex(x => x.id === parseInt(nameId[1]));
    orderOptionList[foundIndex][nameId[0]] = e.target.value;
    setEdited(true)
  }

  const updateTheOrderList = () => {
    setOrderOptionList(localStorage.getItem('orderOptionList') && JSON.parse(localStorage.getItem('orderOptionList')) || []);
  }

  const resetInput = () => {
    setBuyAt("")
    setBuyAtStoploss("")
    setBuyAtTarget("")

    setBuyAtBelow("")
    setBuyAtBelowStoploss("")
    setBuyAtBelowTarget("")

    setSellAt("")
    setSellAtStoploss("")
    setSellAtTarget("")

    setSellAtAbove("");
    setSellAtAboveStoploss("")
    setSellAtAboveTarget("")

    // setstrikeLegCE(getdefaultstrikeLeg());
    // setstrikeLegPE(getdefaultstrikeLeg());

    // setnoOfLotCE(1);
    // setnoOfLotPE(1);

  }
  const getStrikePremium = (optionType) => {
    let strikePrice = getStrikePrice(LiveLtp.iv, optionType);
    let nextExpiryOption = getNextExpiryOption(strikePrice, optionType);
    let perTradeCapital = 25000 / 5;

    if (nextExpiryOption) {
      AdminService.get_quotes({ token: nextExpiryOption.token, exchange: nextExpiryOption.exch_seg })
        .then((res) => {
          let ltpdata = res.data;
          console.log(strikePrice, nextExpiryOption, 'res strick', ltpdata.strprc)

          let qty = perTradeCapital / ltpdata.lp;
          let lots = Math.round(qty / 25);
          if (optionType === "CE") {
            setCeLtp(ltpdata.lp)
          //  setnoOfLotCE(lots);
          setnoOfLotCESuggested(lots)
          } else {
            setPeLtp(ltpdata.lp)
            setnoOfLotPESuggested(lots)
          //  setnoOfLotPE(lots);
          }
        });
    }


    //   var data = {
    //     "exchange": "NSE",
    //     "tradingsymbol": "BANKNIFTY",
    //     "symboltoken": "26009",
    // }
    // AdminService.get_quotes({token: 26009, exchange:'NSE' })
    // .then((res) => {
    //     console.log('res', res.ltp)
    //   });

  }

  const setBuyHighLow = (action) => {
    let starttime = (5 * 60000);
    let data = {
          "exchange": "NSE",
          "token": '26009',
          "interval": 5 , 
          "starttime": Math.floor((new Date().getTime() - starttime) / 1000).toString(),
          "endtime":  Math.floor( new Date(new Date()).getTime()/1000).toString(),
      }
      AdminService.getHistoryShoonya(data).then(res => {
          var candleData = res.data;
          let currentCandle = candleData[0]; 
          if(currentCandle && action == 'candle_high'){
            setBuyAt(parseFloat(currentCandle.inth) +1)
            setBuyAtStoploss(parseFloat(currentCandle.intl) +1)
            setBuyAtTarget(parseFloat(currentCandle.inth) + (parseFloat(currentCandle.inth) - parseFloat(currentCandle.intl)))
          }
          console.log('currentCandle', currentCandle)

          if(currentCandle && action == 'candle_Low'){
            setSellAt(parseFloat(currentCandle.intl) -1)
            setSellAtStoploss(parseFloat(currentCandle.inth) +1)
            setSellAtTarget(parseFloat(currentCandle.intl) - (parseFloat(currentCandle.inth) - parseFloat(currentCandle.intl)))
          }
        
      })    
  };

  return (
    <React.Fragment>
      <Paper
        style={{ overflow: "auto", padding: "5px" }}
      >
        <Typography color="primary" style={{ textAlign: "center" }} gutterBottom>
          Place Shoonya Order <Button variant="contained" onClick={() => resetInput()}>Reset all</Button> &nbsp;&nbsp;
          <TextField
            label="Above Alert"
            placeholder='Above Alert'
          //  variant="outlined"
            type="number"
            name="alertPrice"
            style={{ width: '125px' }}
            value={alertPrice}
            onChange={(event) => setAlertPrice(event.target.value)}
          />&nbsp;&nbsp;
           <TextField
            label="Below Alert"
            placeholder='Below Alert'
           // variant="outlined"
            type="number"
            name="alertPriceDown"
            style={{ width: '125px' }}
            value={alertPriceDown}
            onChange={(event) => setalertPriceDown(event.target.value)}
          />
        </Typography>
        <br />
        <Grid justifyContent='center' spacing={3} container>
          <Grid item>
            <Paper style={{ overflow: "auto", padding: "10px", background: "springgreen" }}>


              <Typography color="primary" gutterBottom>
                CE   Strike
                <input
                  label={`Strike Leg`}
                  type="number"
                  name="strikeLegCE"
                  style={{ width: '30px', textAlign: "center" }}
                  value={strikeLegCE}
                  onChange={(event) => setstrikeLegCE(event.target.value)}
                />
                {getStrikePrice(LiveLtp.iv, 'CE')}@{ceLtp}

                &nbsp;&nbsp;&nbsp;{noOfLotCESuggested} Lot<input 
                  title={"Suggested lot: " + noOfLotCESuggested}
                  label={`Strike Leg`}
                  type="number"
                  name="noOfLotCE"
                  style={{ width: '30px', textAlign: "center" }}
                  value={noOfLotCE}
                  onChange={(event) => setnoOfLotCE(event.target.value)}
                />
               <span title={noOfLotCE * 25 * ceLtp}> Qty:{noOfLotCE * 25}&nbsp;&nbsp; </span>
                <Button variant="contained" 
                 id={"ceMarketBuy"}
                 onClick={() => {
                  // if (window.confirm("Are you sure CALL buy market order?")) {
                  //   placeOptionSPLevelOver(LiveLtp.iv, 'CE', 'market')
                  // }
                  placeOptionSPLevelOver(LiveLtp.iv, 'CE', 'market')
                }}> CE Market Buy</Button>

              </Typography>
              <Paper style={{ overflow: "auto", padding: "10px", background: "azure" }}>


                <Typography>
                  <span title=' LiveLtp >= buyAt '> <b>&#x2191;</b> CE |</span>   <b>{LiveLtp.iv}</b>
                </Typography>
                <Grid item>
                  <ButtonGroup size="small" aria-label="small button group">

                    <TextField
                      label="Buy Above"
                      type="number"
                      name="buyAt"
                      style={{ width: '125px' }}
                      value={buyAt}
                      onChange={(event) => setBuyAt(event.target.value)}
                    />
                    <TextField
                      label={`Stoploss ${parseInt(buyAt - buyAtStoploss)}`}
                      type="number"
                      name="buyAtStoploss"
                      style={{ width: '100px' }}
                      value={buyAtStoploss}
                      onChange={(event) => setBuyAtStoploss(event.target.value)}
                    />
                    <TextField
                      label={`Target ${parseInt(buyAtTarget - buyAt)}`}
                      type="number"
                      name="buyAtTarget"
                      style={{ width: '100px' }}
                      value={buyAtTarget}
                      onChange={(event) => setBuyAtTarget(event.target.value)}
                    />
                    <Button variant="contained" onClick={() => {
                      setBuyAt("")
                      setBuyAtStoploss("")
                      setBuyAtTarget("")
                    }}>
                      <ClearIcon color="error" />
                    </Button>
                  </ButtonGroup>
                  <br />
                  {/* <button onClick={() => {
                  setBuyAt(LiveLtp.iv)
                  navigator.clipboard.writeText(LiveLtp.iv);
                  }}>
                  Copy {LiveLtp.iv}
                </button> */}
                  <div style={{ marginTop: '5px' }}>

                    <button variant="contained" onClick={() => {
                      setBuyAt(LiveLtp.iv)
                      setBuyAtStoploss(LiveLtp.iv - 50)
                      setBuyAtTarget(LiveLtp.iv + 50)
                    }}>
                      SL 50 | T. 50
                    </button>&nbsp;
                    <button title='SL 100 | Target 100' onClick={() => {
                      setBuyAt(LiveLtp.iv)
                      setBuyAtStoploss(LiveLtp.iv - 100)
                      setBuyAtTarget(LiveLtp.iv + 100)
                    }}>
                      SL 100 | T. 100
                    </button>&nbsp;
                    <button onClick={() => {
                      setBuyHighLow('candle_high'); 
                    }}>
                     Candle High
                    </button>&nbsp;
                  </div>

                  {/* LiveLtp >= buyAt */}
                </Grid>
              </Paper>
              <br />
              <Paper style={{ overflow: "auto", padding: "10px", background: "beige" }}>
                <Typography>
                  <span title=' LiveLtp &lt;= buyAtBelow '> <b>&#x2193;</b> CE |</span>   <b>{LiveLtp.iv}</b>
                </Typography>
                <Grid item>
                  <ButtonGroup size="small" aria-label="small button group">
                    <TextField
                      label="Buy Below"
                      type="number"
                      style={{ width: '125px' }}
                      name="buyAtBelow"
                      value={buyAtBelow}
                      onChange={(event) => setBuyAtBelow(event.target.value)}
                    />
                    <TextField
                      label={`Stoploss ${parseInt(buyAtBelow - buyAtBelowStoploss)}`}
                      type="number"
                      name="buyAtBelowStoploss"
                      style={{ width: '100px' }}
                      value={buyAtBelowStoploss}
                      onChange={(event) => setBuyAtBelowStoploss(event.target.value)}
                    />
                    <TextField
                      label={`Target ${parseInt(buyAtBelowTarget - buyAtBelow)}`}
                      type="number"
                      name="buyAtBelowTarget"
                      style={{ width: '100px' }}
                      value={buyAtBelowTarget}
                      onChange={(event) => setBuyAtBelowTarget(event.target.value)}
                    />
                    <Button variant="contained" onClick={() => {
                      setBuyAtBelow("")
                      setBuyAtBelowStoploss("")
                      setBuyAtBelowTarget("")
                    }}>
                      <ClearIcon color="error" />
                    </Button>
                  </ButtonGroup>
                  <br />
                  {/* <button onClick={() => {
                  setBuyAtBelow(LiveLtp.iv)
                  navigator.clipboard.writeText(LiveLtp.iv);
                }}>
                  Copy {LiveLtp.iv} 
                </button>   */}
                  <div style={{ marginTop: '5px' }}>

                    <button title='SL 40 | Target 50' onClick={() => {
                      setBuyAtBelow(LiveLtp.iv)
                      setBuyAtBelowStoploss(LiveLtp.iv - 40)
                      setBuyAtBelowTarget(LiveLtp.iv + 40)
                    }}>
                      SL 40 | T. 40
                    </button>&nbsp;
                    <button title='SL 70 | Target 70' onClick={() => {
                      setBuyAtBelow(LiveLtp.iv)
                      setBuyAtBelowStoploss(LiveLtp.iv - 70)
                      setBuyAtBelowTarget(LiveLtp.iv + 70)
                    }}>
                      SL 70 | T. 70
                    </button>&nbsp;
                    <button title='SL 100 | Target 100' onClick={() => {
                      setBuyAtBelow(LiveLtp.iv)
                      setBuyAtBelowStoploss(LiveLtp.iv - 100)
                      setBuyAtBelowTarget(LiveLtp.iv + 100)
                    }}>
                      SL 100 | T. 100
                    </button>&nbsp;
                  </div>
                  {/* LiveLtp &lt;= buyAtBelow */}

                </Grid>
              </Paper>
              <Grid item>
                <Button
                  fullWidth
                  variant="contained"
                  color="primary"
                  style={{ marginTop: "10px" }}
                  onClick={() => addInOrderPenidngList()}
                >
                  Add to Place Order List
                </Button>
              </Grid>

            </Paper>
          </Grid>
          <Grid item>
            <Paper style={{ overflow: "auto", padding: "10px", background: "khaki" }}>

              <Typography color="primary" gutterBottom>
                PE  Strike<input
                  label={`Strike Leg`}
                  type="number"
                  name="strikeLegPE"
                  style={{ width: '45px', textAlign: "center" }}
                  value={strikeLegPE}
                  onChange={(event) => setstrikeLegPE(event.target.value)}
                />{getStrikePrice(LiveLtp.iv, 'PE')}@{peLtp}
                &nbsp;&nbsp;&nbsp;{noOfLotPESuggested} Lot<input
                  title={"Suggested lot: " + noOfLotPESuggested}
                  label={`Strike Leg`}
                  type="number"
                  name="noOfLotPE"
                  style={{ width: '30px', textAlign: "center" }}
                  value={noOfLotPE}
                  onChange={(event) => setnoOfLotPE(event.target.value)}
                />
                <span title={noOfLotPE * 25 * peLtp}> Qty:{noOfLotPE * 25}&nbsp;&nbsp; </span>

                <Button variant="contained" 
                  id={"peMarketBuy"}
                onClick={() => {
                  // if (window.confirm("Are you sure PUT buy market order?")) {
                    
                  // }
                  placeOptionSPLevelOver(LiveLtp.iv, 'PE', 'market')
                }}> PE Market Buy</Button>
                &nbsp;
              </Typography>
              <Paper style={{ overflow: "auto", padding: "10px", background: "beige" }}>
                <Typography>
                  <span title=' LiveLtp &lt;= sellAt '> <b>&#x2193;</b> PE |</span>   <b>{LiveLtp.iv}</b>
                </Typography>

                <Grid item>
                  <ButtonGroup size="small" aria-label="small button group">
                    <TextField
                      label="Sell Below"
                      type="number"
                      style={{ width: '125px' }}
                      name="sellAt"
                      value={sellAt}
                      onChange={(event) => setSellAt(event.target.value)}
                    />
                    <TextField
                      label={`Stoploss ${parseInt(sellAtStoploss - sellAt)}`}
                      type="number"
                      name="sellAtStoploss"
                      style={{ width: '100px' }}
                      value={sellAtStoploss}
                      onChange={(event) => setSellAtStoploss(event.target.value)}
                    />
                    <TextField
                      label={`Target ${parseInt(sellAt - sellAtTarget)}`}
                      type="number"
                      name="sellAtTarget"
                      style={{ width: '100px' }}
                      value={sellAtTarget}
                      onChange={(event) => setSellAtTarget(event.target.value)}
                    />
                    <Button variant="contained" onClick={() => {
                      setSellAt("")
                      setSellAtStoploss("")
                      setSellAtTarget("")
                    }}>
                      <ClearIcon color="error" />
                    </Button>
                  </ButtonGroup>

                  <br />
                  {/* <button onClick={() => {
                navigator.clipboard.writeText(LiveLtp.iv);
                setSellAt(LiveLtp.iv)
              }}>
                Copy {LiveLtp.iv}
              </button>   */}
                  <div style={{ marginTop: '5px' }}>

                    <button  onClick={() => {
                      setSellAt(LiveLtp.iv)
                      setSellAtStoploss(LiveLtp.iv + 50)
                      setSellAtTarget(LiveLtp.iv - 50)
                    }}>
                      SL 50 | T. 50
                    </button>&nbsp;
                 
                    <button title='SL 100 | Target 100' onClick={() => {
                      setSellAt(LiveLtp.iv)
                      setSellAtStoploss(LiveLtp.iv + 100)
                      setSellAtTarget(LiveLtp.iv - 100)
                    }}>
                      SL 100 | T. 100
                    </button>&nbsp;
                    <button onClick={() => {
                      setBuyHighLow('candle_Low'); 
                    }}>
                     Candle Low
                    </button>&nbsp;
                  </div>
                  {/* LiveLtp &lt;= sellAt */}
                </Grid>
              </Paper>
              <br />


              <Paper style={{ overflow: "auto", padding: "10px", background: "azure" }}>
                <Typography>
                <span title=' LiveLtp >= sellAtAbove '> <b>&#x2191;</b> PE |</span>   <b>{LiveLtp.iv}</b>
                </Typography>
                <Grid item>
                  <ButtonGroup size="small" aria-label="small button group">
                    <TextField
                      label="Sell Above"
                      type="number"
                      style={{ width: '125px' }}
                      name="sellAtAbove"
                      value={sellAtAbove}
                      onChange={(event) => setSellAtAbove(event.target.value)}
                    />
                    <TextField
                      label={`Stoploss ${parseInt(sellAtAboveStoploss - sellAtAbove)}`}
                      type="number"
                      name="sellAtAboveStoploss"
                      style={{ width: '100px' }}
                      value={sellAtAboveStoploss}
                      onChange={(event) => setSellAtAboveStoploss(event.target.value)}
                    />
                    <TextField
                      label={`Target ${parseInt(sellAtAbove - sellAtAboveTarget)}`}
                      type="number"
                      name="sellAtAboveTarget"
                      style={{ width: '100px' }}
                      value={sellAtAboveTarget}
                      onChange={(event) => setSellAtAboveTarget(event.target.value)}
                    />
                    <Button variant="contained" onClick={() => {
                      setSellAtAbove("");
                      setSellAtAboveStoploss("")
                      setSellAtAboveTarget("")
                    }}>
                      <ClearIcon color="error" />
                    </Button>
                  </ButtonGroup>
                  <br />
                  {/* <button onClick={() => {
                navigator.clipboard.writeText(LiveLtp.iv);
                setSellAtAbove(LiveLtp.iv)
              }}>
                Copy {LiveLtp.iv}
              </button>   */}
                  <div style={{ marginTop: '5px' }}>

                    <button title='SL 50 | Target 50' onClick={() => {
                      setSellAtAbove(LiveLtp.iv)
                      setSellAtAboveStoploss(LiveLtp.iv + 50)
                      setSellAtAboveTarget(LiveLtp.iv - 50)
                    }}>
                      SL 50 | T. 50
                    </button>&nbsp;
                    <button title='SL 70 | Target 70' onClick={() => {
                      setSellAtAbove(LiveLtp.iv)
                      setSellAtAboveStoploss(LiveLtp.iv + 70)
                      setSellAtAboveTarget(LiveLtp.iv - 70)
                    }}>
                      SL 70 | T. 70
                    </button>&nbsp;
                    <button title='SL 100 | Target 100' onClick={() => {
                      setSellAtAbove(LiveLtp.iv)
                      setSellAtAboveStoploss(LiveLtp.iv + 100)
                      setSellAtAboveTarget(LiveLtp.iv - 100)
                    }}>
                      SL 100 | T. 100
                    </button>&nbsp;
                   
                  </div>
                  {/* LiveLtp >= sellAtAbove */}
                </Grid>
              </Paper>
              <Grid item>
                <Button
                  color="secondary"
                  fullWidth
                  variant="contained"
                  style={{ marginTop: "10px" }}
                  onClick={() => addInOrderPenidngList()}
                >
                  Add to Place Order List
                </Button>
              </Grid>
            </Paper>
          </Grid>

          <Grid item>
            <Paper style={{ overflow: "auto", padding: "5px", background: "khaki" }}>
              <Typography color="primary" gutterBottom>
                    Higher High and Lower Low
                    <AutoTradingSwitch />
              </Typography>
            </Paper>

            <Paper style={{ overflow: "auto", padding: "5px", background: "lightgreen" }}>
                <EmaBuySwitch updateTheOrderList={updateTheOrderList} />
            </Paper>
        
            <Paper style={{ overflow: "auto", padding: "5px", background: "pink" }}>
              <EmaSellSwitch updateTheOrderList={updateTheOrderList} />
            </Paper>

            <Paper style={{ overflow: "auto", padding: "5px", background: "aquamarine" }}>
              <CrossMABuySellSwitch LiveLtp={LiveLtp.iv} updateTheOrderList={updateTheOrderList} />
            </Paper>

            {/* <br />
            <Paper style={{ overflow: "auto", padding: "10px", background: "khaki" }}>
               <CollectSmallProfitSwitch />
            </Paper> */}

           
            
          </Grid>
        </Grid>
        <br />
        <Table size="small" aria-label="sticky table">
          <TableHead variant="head">
            <TableRow key="1" variant="head">
              <TableCell className="TableHeadFormat" align="left">
                Time ({orderOptionList.length})
              </TableCell>

              <TableCell className="TableHeadFormat" align="left">
                Origin
              </TableCell>


              <TableCell className="TableHeadFormat" align="left">
                B. Above
              </TableCell>
              <TableCell className="TableHeadFormat" align="left">
                B. Above SL
              </TableCell>
              <TableCell className="TableHeadFormat" align="left">
                B. Above T.
              </TableCell>

              <TableCell className="TableHeadFormat" align="left">
                B. Below
              </TableCell>
              <TableCell className="TableHeadFormat" align="left">
                B. Below SL
              </TableCell> <TableCell className="TableHeadFormat" align="left">
                B. Below T.
              </TableCell>

              <TableCell className="TableHeadFormat" align="left">
                S. Below
              </TableCell>
              <TableCell className="TableHeadFormat" align="left">
                S. Below SL
              </TableCell> <TableCell className="TableHeadFormat" align="left">
                S. Below T.
              </TableCell>
              <TableCell className="TableHeadFormat" align="left">
                S. Above
              </TableCell>
              <TableCell className="TableHeadFormat" align="left">
                S. Above SL
              </TableCell><TableCell className="TableHeadFormat" align="left">
                S. Above T.
              </TableCell>

              <TableCell className="TableHeadFormat" align="left">
                Delete <Button variant='contained' onClick={() => updateTheOrderList()}>Refresh</Button>
              </TableCell>
            </TableRow>
          </TableHead>

          <TableBody id="tableAdd" style={{ width: "", whiteSpace: "nowrap" }}>
            {orderOptionList.length ?
              orderOptionList.map((row, i) => (
                <TableRow hover>
                  <TableCell align="left">{row.createdAt}</TableCell>
                  <TableCell align="left">{row.origin}</TableCell>

                  <TableCell align="left">
                    {row.buyAt ? (
                      <input
                        step="1"
                        style={{ width: "75px", textAlign: "center" }}
                        type="number"
                        value={row.buyAt}
                        name={`buyAt-${row.id}`}
                        onChange={orderValueChange}
                      />
                    ) : (
                      "-"
                    )}
                  </TableCell>
                  <TableCell align="left">
                    {row.buyAtStoploss ? (
                      <input
                        step="1"
                        style={{ width: "75px", textAlign: "center" }}
                        type="number"
                        value={row.buyAtStoploss}
                        name={`buyAtStoploss-${row.id}`}
                        onChange={orderValueChange}
                      />
                    ) : (
                      "-"
                    )}
                  </TableCell> <TableCell align="left">
                    {row.buyAtTarget ? (
                      <input
                        step="1"
                        style={{ width: "75px", textAlign: "center" }}
                        type="number"
                        value={row.buyAtTarget}
                        name={`buyAtTarget-${row.id}`}
                        onChange={orderValueChange}
                      />
                    ) : (
                      "-"
                    )}
                  </TableCell>
                  <TableCell align="left">
                    {row.buyAtBelow ? (
                      <input
                        step="1"
                        value={row.buyAtBelow}
                        style={{ width: "75px", textAlign: "center" }}
                        type="number"
                        name={`buyAtBelow-${row.id}`}
                        onChange={orderValueChange}
                      />
                    ) : (
                      "-"
                    )}
                  </TableCell>
                  <TableCell align="left">
                    {row.buyAtBelowStoploss ? (
                      <input
                        step="1"
                        value={row.buyAtBelowStoploss}
                        style={{ width: "75px", textAlign: "center" }}
                        type="number"
                        name={`buyAtBelowStoploss-${row.id}`}
                        onChange={orderValueChange}
                      />
                    ) : (
                      "-"
                    )}
                  </TableCell><TableCell align="left">
                    {row.buyAtBelowTarget ? (
                      <input
                        step="1"
                        value={row.buyAtBelowTarget}
                        style={{ width: "75px", textAlign: "center" }}
                        type="number"
                        name={`buyAtBelowTarget-${row.id}`}
                        onChange={orderValueChange}
                      />
                    ) : (
                      "-"
                    )}
                  </TableCell>

                  <TableCell align="left">
                    {row.sellAt ? (
                      <input
                        step="1"
                        value={row.sellAt}
                        style={{ width: "75px", textAlign: "center" }}
                        type="number"
                        name={`sellAt-${row.id}`}
                        onChange={orderValueChange}
                      />
                    ) : (
                      "-"
                    )}
                  </TableCell>
                  <TableCell align="left">
                    {row.sellAtStoploss ? (
                      <input
                        step="1"
                        value={row.sellAtStoploss}
                        style={{ width: "75px", textAlign: "center" }}
                        type="number"
                        name={`sellAtStoploss-${row.id}`}
                        onChange={orderValueChange}
                      />
                    ) : (
                      "-"
                    )}
                  </TableCell>  <TableCell align="left">
                    {row.sellAtTarget ? (
                      <input
                        step="1"
                        value={row.sellAtTarget}
                        style={{ width: "75px", textAlign: "center" }}
                        type="number"
                        name={`sellAtTarget-${row.id}`}
                        onChange={orderValueChange}
                      />
                    ) : (
                      "-"
                    )}
                  </TableCell>
                  <TableCell align="left">
                    {row.sellAtAbove ? (
                      <input
                        value={row.sellAtAbove}
                        step="1"
                        style={{ width: "75px", textAlign: "center" }}
                        type="number"
                        name={`sellAtAbove-${row.id}`}
                        onChange={orderValueChange}
                      />
                    ) : (
                      "-"
                    )}
                  </TableCell>
                  <TableCell align="left">
                    {row.sellAtAboveStoploss ? (
                      <input
                        value={row.sellAtAboveStoploss}
                        step="1"
                        style={{ width: "75px", textAlign: "center" }}
                        type="number"
                        name={`sellAtAboveStoploss-${row.id}`}
                        onChange={orderValueChange}
                      />
                    ) : (
                      "-"
                    )}
                  </TableCell>
                  <TableCell align="left">
                    {row.sellAtAboveTarget ? (
                      <input
                        value={row.sellAtAboveTarget}
                        step="1"
                        style={{ width: "75px", textAlign: "center" }}
                        type="number"
                        name={`sellAtAboveTarget-${row.id}`}
                        onChange={orderValueChange}
                      />
                    ) : (
                      "-"
                    )}
                  </TableCell>

                  <TableCell align="left">
                    <Button color='secondary' onClick={() => setDeleteId(row.id)}>  <DeleteIcon
                      style={{ cursor: "pointer" }}
                      
                    />Delete</Button>
                   
                  </TableCell>
                </TableRow>
              )) : ""}
          </TableBody>
        </Table>
      </Paper>
    </React.Fragment>
  );
});

BNOptionBuyAtLevel.propTypes = {
  LiveLtp: PropTypes.string.isRequired,
};

export default BNOptionBuyAtLevel;
