import React from "react";
import AdminService from "../service/AdminService";
import Typography from "@material-ui/core/Typography";
import Button from "@material-ui/core/Button";
import Table from "@material-ui/core/Table";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import TableCell from "@material-ui/core/TableCell";
import TableBody from "@material-ui/core/TableBody";
import Grid from "@material-ui/core/Grid";
import Paper from "@material-ui/core/Paper";
import PostLoginNavBar from "../PostLoginNavbar";
import { resolveResponse } from "../../utils/ResponseHandler";
import Spinner from "react-spinner-material";
import TextField from "@material-ui/core/TextField";
import * as moment from 'moment';

class OrderBook extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            oderbookData: localStorage.getItem('oderbookData') && JSON.parse(localStorage.getItem('oderbookData')) || [],
            listofzones: [],
            selectedZone: [],
            zone: '',
            selectAllzone: 'Select All',
            triggerprice: 0,
            price: 0,
            lotsize: 0,
            firstTimeFlag: true,
            totalPoint: 0,
            totalPnl: 0,
            trackSLPrice: localStorage.getItem('trackSLPrice') && JSON.parse(localStorage.getItem('trackSLPrice')) || [],
        }
    }

    getTodayOrder = () => {
        this.setState({ totalPoint: 0 });
        let trackSLPrice = localStorage.getItem('trackSLPrice') && JSON.parse(localStorage.getItem('trackSLPrice')) || [];
        AdminService.getOrderListShoonya()
            .then((res) => {
                let orderlist = res.data;
                this.setState({ oderbookData: orderlist });
                localStorage.setItem('oderbookData', JSON.stringify(orderlist));
                this.props.refreshOrderbookFlagOff()

                let livebnPriceSecondbySecond = localStorage.getItem('livebnPriceSecondbySecond') ? JSON.parse(localStorage.getItem('livebnPriceSecondbySecond')) : [];
                this.state.oderbookData.length && this.state.oderbookData.forEach(elementO => {
                    let found = livebnPriceSecondbySecond.filter(item => item.t == elementO.norentm);
                    if (found.length) {
                        elementO.spot_price = found[0].iv;
                        return
                    }
                });

                this.setState({ oderbookData: this.state.oderbookData }, function () {
                    this.getEarnLossDiffPoints()
                });


            });
    }

    // componentWillReceiveProps(nextProps) {
    //     console.log("this.props.orderbookRefresh", nextProps)
    //     if(this.props.orderbookRefresh){
    //         this.getTodayOrder();
    //     }
    //   }

    //   componentDidUpdate(prevProps) {
    //     console.log("componentDidUpdate", prevProps)
    //     if (prevProps.text !== this.props.text) {
    //      // this.updateAndNotify();
    //      this.getTodayOrder();
    //     }
    //   }

    //   shouldComponentUpdate(nextProps) {
    //     console.log("shouldComponentUpdate", nextProps)
    //     if(nextProps.orderbookRefresh){
    //         this.getTodayOrder();
    //     }
    //   }
    componentDidMount() {


        this.getTodayOrder();

        // this.state.oderbookData.forEach(element => {
        //     if(element.status === 'COMPLETE'){
        //         element.spot_price = element.tsym; 
        //         return
        //     }
        // });
        // let livebnPriceSecondbySecond = localStorage.getItem('livebnPriceSecondbySecond') ? JSON.parse(localStorage.getItem('livebnPriceSecondbySecond')) : []; 
        // this.state.oderbookData.length && this.state.oderbookData.forEach(elementO => {
        //     let found =  livebnPriceSecondbySecond.filter( item => item.t == elementO.norentm);
        //     if(found.length){
        //         elementO.spot_price = found[0].iv; 
        //         return
        //     }
        // });
        // this.setState({oderbookData: this.state.oderbookData}, function(){ 
        //     this.getEarnLossDiffPoints()
        //  });

     
    }

    getEarnLossDiffPoints = () => {
        let lastSym = '', tradeIn = 0, tradeOut = 0, inAvgPrice = 0, outAvgPrice = 0, totalPnl = 0;
        this.setState({ totalPnl: 0 })
        // let oderbookData = this.state.oderbookData;
        // console.log('oderbookData before sort',oderbookData)
        // oderbookData.sort(function(a,b){
        //     return moment(b.exch_tm.split(" ")[1], 'HH:mm:ss') - moment(a.exch_tm.split(" ")[1], 'HH:mm:ss');
        //   });
        //   console.log('oderbookData after sort',oderbookData)

        for (let index = this.state.oderbookData.length - 1; index >= 0; index--) {
            const elementOut = this.state.oderbookData[index];
            if (elementOut.status === 'COMPLETE' && elementOut.trantype === "B") {
                lastSym = elementOut.tsym;
                tradeIn = parseFloat(elementOut.spot_price);
                inAvgPrice = elementOut.avgprc;

                let optiontype = elementOut.dname && elementOut.dname.trim().split(' ')[3];
                // console.log('in',lastSym, optiontype,  tradeIn, elementOut.exch_tm)


                for (let indexIn = index; indexIn >= 0; indexIn--) {
                    const elementIn = this.state.oderbookData[indexIn];
                    if (elementIn.tsym === lastSym && elementIn.status === 'COMPLETE' && elementIn.trantype === 'S') {
                        tradeOut = parseFloat(elementIn.spot_price);
                        outAvgPrice = elementIn.avgprc;
                        //    console.log('out', optiontype, elementIn,   tradeOut, elementIn.exch_tm); 
                        break;
                    }
                }
                if (optiontype === 'CE') {
                    let point = (tradeOut - tradeIn);
                    elementOut.point = point.toFixed(2);
                    this.setState({ totalPoint: point ? (this.state.totalPoint += point) : this.state.totalPoint })
                }
                if (optiontype === 'PE') {
                    let point = (tradeIn - tradeOut);
                    //  console.log('PE', tradeIn, tradeOut,  tradeIn - tradeOut)
                    elementOut.point = point.toFixed(2);
                    this.setState({ totalPoint: point ? (this.state.totalPoint += point) : this.state.totalPoint })
                }
                let pnl = ((parseFloat(outAvgPrice) - parseFloat(inAvgPrice)) * parseFloat(elementOut.qty));
                let pnlPer = ((parseFloat(outAvgPrice) - parseFloat(inAvgPrice)) * 100 / parseFloat(inAvgPrice));

                totalPnl += pnl;
                elementOut.pnl = pnl.toFixed(2) + ' (' +pnlPer.toFixed(2)+'%)' ;
            }
        }
        this.setState({ oderbookData: this.state.oderbookData, totalPnl: totalPnl }, function () {

        });

    }


    cancelOrderOfSame = (orderId, variety) => {

        var data = {
            "variety": variety,
            "orderid": orderId,
        }
        AdminService.cancelOrder(data).then(res => {
            let data = resolveResponse(res);
            if (data.status && data.message === 'SUCCESS') {
                //  console.log("cancel order", data);   
                this.getTodayOrder();
                // this.setState({ orderid : data.data && data.data.orderid });
            }
        })

    }




    modifyOrder = (row, trailingstoploss) => {


        var data = {
            "variety": row.variety,  // "STOPLOSS",
            "orderid": row.orderid,
            "ordertype": this.state.price !== 0 ? "STOPLOSS_LIMIT" : "STOPLOSS_MARKET",
            "producttype": row.producttype, //"DELIVERY",
            "duration": row.duration,
            "price": this.state.price,
            "triggerprice": trailingstoploss || this.state.triggerprice,
            "quantity": row.quantity,
            "tradingsymbol": row.tradingsymbol,
            "symboltoken": row.symboltoken,
            "exchange": row.exchange
        }
        AdminService.modifyOrder(data).then(res => {
            let data = resolveResponse(res);
            // console.log(data);   
            if (data.status && data.message === 'SUCCESS') {
                // localStorage.setItem('ifNotBought' ,  'false')
                this.setState({ triggerprice: 0 });
                this.getTodayOrder();
            }
        })
    }

    onChange = (e) => {
        this.setState({ [e.target.name]: e.target.value.trim() });
    }

    onChangePriceStopLoss = (e) => {
        this.setState({ [e.target.name]: e.target.value.trim() });
        let tradingsymbol = e.target.name.split('_')[1];
        let trackSLPrice = localStorage.getItem('trackSLPrice') && JSON.parse(localStorage.getItem('trackSLPrice')) || [];
        let isfound = false;
        for (let index = 0; index < trackSLPrice.length; index++) {
            const element = trackSLPrice[index];
            if (element.tradingsymbol === tradingsymbol) {
                element.priceStopLoss = e.target.value.trim();
                isfound = true;
                break;
            }
        }
        if (!isfound) {
            trackSLPrice.push({
                priceStopLoss: e.target.value.trim(),
                tradingsymbol: tradingsymbol,
                optiontype: tradingsymbol.substr(tradingsymbol.length - 2, tradingsymbol.length - 1),
                name: tradingsymbol.includes('BANKNIFTY') ? 'BANKNIFTY' : "NIFTY"
            });
        }

        //    console.log("slprice tradingsymbol", trackSLPrice , tradingsymbol, isfound)
        localStorage.setItem('trackSLPrice', JSON.stringify(trackSLPrice));
    }

    onChangePriceTarget = (e) => {
        this.setState({ [e.target.name]: e.target.value.trim() });
        let tradingsymbol = e.target.name.split('_')[1];
        let trackSLPrice = localStorage.getItem('trackSLPrice') && JSON.parse(localStorage.getItem('trackSLPrice')) || [];
        let isfound = false;
        for (let index = 0; index < trackSLPrice.length; index++) {
            const element = trackSLPrice[index];
            if (element.tradingsymbol === tradingsymbol) {
                element.priceTarget = e.target.value.trim();
                isfound = true;
                break;
            }
        }
        if (!isfound) {
            trackSLPrice.push({
                priceStopLoss: e.target.value.trim(),
                tradingsymbol: tradingsymbol,
                optiontype: tradingsymbol.substr(tradingsymbol.length - 2, tradingsymbol.length - 1),
                name: tradingsymbol.includes('BANKNIFTY') ? 'BANKNIFTY' : "NIFTY"

            });
        }
        //   console.log("trarget price update", trackSLPrice)
        localStorage.setItem('trackSLPrice', JSON.stringify(trackSLPrice));
    }


    render() {
        //   console.log(this.state.oderbookData);

        return (
            <React.Fragment>

                <Grid spacing={1} direction="row" alignItems="center" container>
                    <Grid item xs={12} sm={4} >
                        <Typography color="primary" gutterBottom>
                            Shoonya Oderbook ({this.state.oderbookData.length || 0})
                        </Typography>
                    </Grid>

                    <Grid item xs={12} sm={4} >
                        <Button id="orderRefresh" type="number" variant="contained" style={{ float: "right" }} onClick={() => this.getEarnLossDiffPoints()}>Refresh Points</Button>
                    </Grid>
                    <Grid item xs={12} sm={4} >
                        <Button id="orderRefresh" type="number" variant="contained" style={{ float: "right" }} onClick={() => this.getTodayOrder()}>Refresh Orderbook</Button>
                    </Grid>


                </Grid>

                <Grid direction="row" alignItems="center" container>
                    <Grid item xs={12} sm={12} >

                        <Paper style={{ padding: "20px" }} >

                            <Table size="small" aria-label="sticky table" >

                                <TableHead style={{ whiteSpace: "nowrap" }} variant="head">
                                    <TableRow variant="head" >
                                        <TableCell align="center"><b>Sr.No</b></TableCell>

                                        <TableCell align="center"><b>Update time</b></TableCell>

                                        {/* <TableCell align="center"><b>OrderId</b></TableCell> */}

                                        <TableCell align="center"><b>Trading Symbol</b></TableCell>
                                        <TableCell align="center"><b>Order Type</b></TableCell>

                                        <TableCell align="center"><b>Qty </b>&nbsp;</TableCell>


                                        <TableCell align="center"><b>
                                            <Button onClick={this.state.oderbookData.length && this.state.oderbookData.sort(function (a, b) {
                                                if (a.status < b.status) { return -1; }
                                                if (a.status > b.status) { return 1; }
                                                return 0;
                                            })}> Status </Button> </b>&nbsp;</TableCell>
                                        {/* <TableCell align="center"><b>SLPrice</b>&nbsp;</TableCell>
                              <TableCell align="center"><b>TargetPrice</b></TableCell> */}

                                        {/* <TableCell align="center"><b>Details</b>&nbsp;</TableCell> */}
                                        <TableCell align="center"><b>Spot Price</b></TableCell>

                                        <TableCell align="center"><b>AvgPrice</b>&nbsp;</TableCell>


                                        <TableCell align="center" title="Point earn or loss"><b>Point({this.state.totalPoint.toFixed(2)})</b></TableCell>
                                        <TableCell align="center" title="pnl"><b>PnL({this.state.totalPnl.toFixed(2)})</b></TableCell>


                                        {/* <TableCell align="center"><b>Token</b></TableCell> */}

                                    </TableRow>
                                </TableHead>
                                <TableBody>

                                    {this.state.oderbookData.length ? this.state.oderbookData.map((row, i) => (
                                        <TableRow hover key={i} >

                                            <TableCell align="center">{i + 1}</TableCell>

                                            <TableCell align="center">{row.exch_tm || row.norentm}</TableCell>

                                            {/* <TableCell align="center">{row.orderid  }</TableCell> */}

                                            <TableCell align="center">
                                                <Button onClick={() => navigator.clipboard.writeText(row.dname)}>{row.dname}</Button>
                                            </TableCell>
                                            <TableCell align="center">{row.trantype == "B" ? <span style={{ color: "green" }}>BUY </span> : <span style={{ color: "red" }}>SELL</span>}</TableCell>
                                            {/* <TableCell align="center">{row.prctyp}</TableCell> */}

                                            {/* <TableCell align="center">{row.producttype}</TableCell> */}



                                            <TableCell align="center">
                                                {/* {row.orderstatus === 'trigger pending' ? 
                                        <TextField type="number" style={{textAlign:'center', width:'50px'}} id="lotsize"  value={row.quantity}  name="lotsize" onChange={this.onChange}/>
                                        : row.quantity} */}
                                                {row.qty}

                                            </TableCell>



                                            <TableCell title={row.rejreason} align="center">

                                                <Button style={{ color: row.status == "REJECTED" ? 'RED' : "" }} title={row.rejreason} onClick={() => navigator.clipboard.writeText(row.dname)}>{row.status}</Button>

                                            </TableCell>
                                            <TableCell align="center">{row.spot_price}</TableCell>

                                            <TableCell align="center">{row.avgprc}</TableCell>



                                            <TableCell align="center">{row.point}</TableCell>
                                            <TableCell align="center">{row.pnl}</TableCell>


                                        </TableRow>
                                    )) : "No Order found"}
                                </TableBody>
                            </Table>
                        </Paper>
                    </Grid>

                </Grid>

            </React.Fragment>
        )
    }

}

