import React, { useContext, useEffect, useState } from 'react';
import PropTypes from 'prop-types';
import AdminService from "../service/AdminService";
import Typography from "@material-ui/core/Typography";
import Button from "@material-ui/core/Button";

import ButtonGroup from "@material-ui/core/ButtonGroup";

import Table from "@material-ui/core/Table";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import TableCell from "@material-ui/core/TableCell";
import TableBody from "@material-ui/core/TableBody";
import Grid from "@material-ui/core/Grid";
import Paper from "@material-ui/core/Paper";
import PostLoginNavBar from "../PostLoginNavbar";
import { resolveResponse } from "../../utils/ResponseHandler";
import Spinner from "react-spinner-material";
import TextField from "@material-ui/core/TextField";
import Autocomplete from '@material-ui/lab/Autocomplete';
import DeleteIcon from '@material-ui/icons/Delete';
import OpenInNewIcon from '@material-ui/icons/OpenInNew';
import CommonMethods from '../../utils/CommonMethods';
import * as moment from 'moment';
import Notify from "../../utils/Notify";
import ShowChartIcon from '@material-ui/icons/ShowChart';
import ClearIcon from '@material-ui/icons/Clear';
const BNOptionBuyAtLevel = ({
  LiveLtp,
}) => {

  const [bnComponents, setBnComponents] = useState([]);  

  useEffect(() => {
    const interval = setInterval(() => {
    //  updateBNCompltp(); 
    }, 10000)

    return () => {
      clearInterval(interval);
    }
  }, [])

  useEffect(()=> {
    bnComponents.forEach(element => {
        element.highAlert = compareHigh(element); 
        if(compareHigh(element)){
          speakIt(`${element.symname} higher high`)
        }
        element.lowAlert = compareLow(element); 
        if(compareLow(element)){
          speakIt(`${element.symname} lower low`)
        }
      });
      setBnComponents(bnComponents);
      localStorage.setItem("bnComponents", JSON.stringify(bnComponents)); 

  }, [bnComponents]);


  const resetInput = () => {
   window.document.location.reload();
  }

    
  // HDFC_Bank 26.00
  // ICICI Bank Ltd. 24.97
  // Kotak Mahindra Bank Ltd. 11.11
  // Axis Bank Ltd. 10.93
  // State Bank of India 10.87
  // IndusInd Bank Ltd. 6.17
  // AU Small Finance Bank Ltd. 2.34
  // Bank of Baroda 1.97
  // Federal Bank Ltd. 1.90
  // Bandhan Bank Ltd. 1.61


   const updateBNCompltp =()=> {
    AdminService.get_banknifty_components()
    .then((res) => {
        let bankList = res.data; 

        bankList.forEach(element => {
        let changePer = ((parseFloat(element.lp) -parseFloat(element.c)) * 100 / parseFloat(element.c)).toFixed(2);
        let changeAmount = ((parseFloat(element.lp) -parseFloat(element.c))).toFixed(2);

        let candleDistance = parseFloat(element.h) - parseFloat(element.l); //high - low
        let strongPer = (((parseFloat(element.lp) - parseFloat(element.l)) * 100) / candleDistance).toFixed(2); // close-low*100/distance 
          element.strongPer = strongPer
          element.weekPer = (100 - strongPer).toFixed(2);
          element.changePer = changePer; 
          element.changeAmount = changeAmount; 

          if(element.symname == "HDFCBANK"){
            element.indexContribution =  ((changePer * 26) / 100).toFixed(2) ; 

          }
          
          // element.highAlert = compareHigh(element); 
          // if(compareHigh(element)){
          //   speakIt(`${element.symname} higher high`)
          // }

          // element.lowAlert = compareLow(element); 
          // if(compareLow(element)){
          //   speakIt(`${element.symname} lower low`)
          // }
        });
        setBnComponents(bankList);
        localStorage.setItem("bnComponents", JSON.stringify(bankList)); 
      });
   }
  const speakIt = (text) => {
    var msg = new SpeechSynthesisUtterance();
    msg.text = text.toLowerCase();
    window.speechSynthesis.speak(msg);
   }

  const compareHigh = (element) => {

    let backupBnComponents = localStorage.getItem("bnComponents") ? JSON.parse(localStorage.getItem("bnComponents")) : []; 
    let found = backupBnComponents.filter(item => item.symname == element.symname); 

      if(found.length) {
      //  console.log(element.symname, 'high', found[0].h,  element.h, 'low', found[0].l,  element.l, new Date().toLocaleTimeString())

        if(element.h > found[0].h){
          console.log('found', found, element.h, found[0].h)

          return true; 
        }
      }
      return false; 
   }
   const compareLow = (element) => {
    let found = bnComponents.filter(item => item.symname == element.symname); 
      if(found.length) {
        if(element.l < found[0].l){
          return true; 
        }
      }
      return false; 
   }


//console.log("bnComponents2", bnComponents)
  return (
    <React.Fragment>
      <Paper
        style={{ overflow: "auto", padding: "10px" }}
      >
        <Typography color="primary" style={{ textAlign: "center" }} gutterBottom>
     
      <Button>  <a target={"_blank"} href={"/mtrade/#/strong-charts"} > Banknifty Components  </a> </Button>  
        <Button variant="contained" onClick={() => updateBNCompltp()}>Refresh</Button>

        </Typography>       
        <Table size="small" aria-label="sticky table">
          <TableHead style={{ whiteSpace: "nowrap" }} variant="head">
            <TableRow key="1" variant="head" style={{ fontWeight: "bold" }}>
            
              <TableCell className="TableHeadFormat" align="left">
                Symbol
              </TableCell>

              <TableCell className="TableHeadFormat" align="left">
                Ltp
              </TableCell>
            
              <TableCell className="TableHeadFormat" align="left">
                Index Con.
              </TableCell>


              <TableCell className="TableHeadFormat" align="left">
                High 
              </TableCell>

              <TableCell className="TableHeadFormat" align="left">
                Low
              </TableCell> 

              <TableCell className="TableHeadFormat" align="left">
                High-to-Low
              </TableCell> 

             
              <TableCell className="TableHeadFormat" align="left">
                Seller 
              </TableCell>

              <TableCell className="TableHeadFormat" align="left">
                Emotions
              </TableCell>
              <TableCell className="TableHeadFormat" align="left">
                Buyer
              </TableCell>
              <TableCell className="TableHeadFormat" align="left">
                Open 
              </TableCell>

              <TableCell title='Previous closed' className="TableHeadFormat" align="left">
                Closed
              </TableCell>
             
              <TableCell className="TableHeadFormat" align="left">
                Volume 
              </TableCell>

              <TableCell className="TableHeadFormat" align="left">
                Time
              </TableCell>
            </TableRow>
          </TableHead>

          <TableBody id="tableAdd" style={{ width: "", whiteSpace: "nowrap" }}>
            {bnComponents.length ?
              bnComponents.map((row, i) => (
                <TableRow  hover>
                  <TableCell style={{color: row.changePer > 0 ? "green" : "red", fontWeight: row.highAlert ? "bold": row.lowAlert ? "bold": ""}} align="left">{row.symname}</TableCell>
                  <TableCell style={{color: row.changePer > 0 ? "green" : "red", fontWeight: row.highAlert ? "bold": row.lowAlert ? "bold": ""}} align="left">{row.lp}({row.changePer || 0}%) {row.changeAmount}</TableCell>
                  <TableCell align="left"> {row.indexContribution}</TableCell>

                  <TableCell title={row.symname} style={{background: row.highAlert ? "green": ""}} align="left">{row.h}{row.highAlert}</TableCell>

                  <TableCell title={row.symname} style={{background: row.lowAlert ? "red": ""}}  align="left">{row.l}{row.lowAlert}</TableCell>
                  <TableCell title={row.symname}  align="left">{(row.h - row.l).toFixed(2)}</TableCell>
                  
                  <TableCell title={row.symname} style={{color: (row.weekPer) > 75 ? "red": "", fontWeight: row.weekPer > 75 ? "bold": ""}} align="left">{row.weekPer}%</TableCell>

                  <TableCell  style={{width: '20%'}} align="left">
                    <progress style={{width: '95%'}} id="file" value={row.strongPer} title={`${row.symname} ${row.strongPer}% buyer`} max="100"> {row.strongPer || 0}% </progress>
                  </TableCell>
                  <TableCell title={row.symname} style={{color: row.strongPer > 75 ? "green": "", fontWeight: row.strongPer > 75 ? "bold": ""}} align="left">{row.strongPer}%</TableCell>


                 
                  <TableCell align="left">{row.o}</TableCell>

                  <TableCell align="left">{row.c}</TableCell>
                  <TableCell align="left">{row.v}</TableCell>
                  <TableCell align="left">{row.request_time.substring(0, 8)}</TableCell>

                </TableRow>
              )) : ""}
          </TableBody>
        </Table>
      </Paper>
    </React.Fragment>
  );
};

BNOptionBuyAtLevel.propTypes = {
  LiveLtp: PropTypes.string.isRequired,
};

export default BNOptionBuyAtLevel;
